<template>
<div>
  <label class="date-label" for="date-filter">Поиск заявок по клиенту
  <input class="date-input" type="search" v-model="searchByCustomer" @input="searchDealsByCustomer" name="date-filter" id="date-filter" placeholder="Введите имя клиента">
  </label>
  <div class="buttons">
  <button type="button" class="filter-button" @click="showClosedDeals">Показать закрытые заявки</button>
  <button type="button" class="filter-button filter-button--red" @click="showOpenedDeals">Показать открытые заявки</button>
  <button type="button" class="filter-button filter-button--grey" @click="showAllDeals">Показать все заявки</button>
  </div>
  <AppTableWrapper>
    <template #table-content>
      <PopupDeleteWrapper
        :hidePopupStatus="popupRemoveHidden"
        @close-popup="popupRemoveToggle({})"
        @delete-element="$emit('remove-deal', currentDeal.id); popupRemoveToggle({})"
        :header="`Удаление заявки ${currentDeal.name}`"
      >
        <template #popup-delete-content>
          После нажатия на иконку корзины будет удалёна заявка <b>{{currentDeal.name}}</b>
        </template>
      </PopupDeleteWrapper>
      <AppTableWrapperRow>
        <template #row-content>
          <th>Название</th>
          <th>Клиент</th>
          <th>Подразделение</th>
          <th>Количество<br>рабочих</th>
          <th>Начало заявки</th>
          <th>Конец заявки</th>
          <th>Заметка</th>
        </template>
      </AppTableWrapperRow>

      <template v-for="element in data">
        <AppTableWrapperRow :key="element.id"
                            @on-click="$router.push({name : 'dealEdit', params: {id: element.id}})"
                           >
          <template #row-content>
            <td :title="element.name">{{element.name}}</td>

            <td class="column-td" :class="(element.neededWorkersCount == element.workers.length)?'column-td--green':'column-td--red'">
              <router-link title="Перейти к клиенту"
                           :to="{name : 'customerAbout', params: {id: element.customer.id}}"
              >
                {{element.customer.name}}
              </router-link>
            </td>
                        <td :title="element.subdivision"  class="column-td" :class="(element.neededWorkersCount == element.workers.length)?'column-td--green':'column-td--red'">
                {{element.subdivision}}
              <!-- </router-link> -->
            </td>
<!--            <td>-->
<!--              <router-link title="Перейти в календарь рабочего"-->
<!--                           :to="{name : 'workerCalendar', params: {id: element.worker.id}}"-->
<!--              >-->
<!--                {{element.worker.surname}} {{element.worker.name}}-->
<!--              </router-link>-->
<!--            </td>-->
            <td :title="element.workers" class="column-td" :class="(element.neededWorkersCount == element.workers.length)?'column-td--green':'column-td--red'">
              <span>{{element.workers.length}}</span>
              <span> / </span>
              <span v-if="element.neededWorkersCount">
               {{element.neededWorkersCount}}
                </span>
                <span v-else>0</span>
                </td>
            <td :title="element.date" class="column-td" :class="(element.neededWorkersCount == element.workers.length)?'column-td--green':'column-td--red'">{{element.dateStart.slice(0, 10)}}</td>
            <td :title="element.date" class="column-td" :class="(element.neededWorkersCount == element.workers.length)?'column-td--green':'column-td--red'">{{element.dateEnd.slice(0, 10)}}</td>
<!-- + ',' + element.dateStart.slice(11) -->
            <td class="column-td" v-if="element.notes"
                :title="element.notes[element.notes.length - 1]"
            :class="(element.neededWorkersCount == element.workers.length)?'column-td--green':'column-td--red'">
              {{element.notes[element.notes.length - 1].slice(10)}}
            </td>
            <td class="column-td" v-else :class="(element.neededWorkersCount == element.workers.length)?'column-td--green':'column-td--red'">Заметок нет</td>

            <td class="row-action">
<!--              <AppButtonIcon icon="description" title="История" size="1.2rem" @button-click="$router.push({name : 'dealHistory', params: {id: element.id}})"/>-->
              <AppButtonIcon icon="create" title="Редактировать" size="1.5rem" @button-click="$router.push({name : 'dealEdit', params: {id: element.id}})"/>
              <AppButtonIcon icon="delete" title="Удалить" size="1.5rem" @button-click="popupRemoveToggle(element)"/>
            </td>
          </template>
        </AppTableWrapperRow>
      </template>
    </template>
  </AppTableWrapper>
</div>
</template>

<script>
import PopupDeleteWrapper from '../../popups/PopupDeleteWrapper'
import AppTableWrapper from '../../table/AppTableWrapper'
import AppTableWrapperRow from '../../table/AppTableWrapperRow'
import AppButtonIcon from '../../AppButtonIcon'
import _ from 'lodash'

export default {
  name: 'DealsTable',

  components: {
    PopupDeleteWrapper,
    AppButtonIcon,
    AppTableWrapper,
    AppTableWrapperRow
  },

  data () {
    return {
      searchByCustomer: '',
      popupRemoveHidden: true,
      currentDeal: {},
      show: false,
      showed: false,
      workersCount: '',
      workersLength: null,
      data: [],
      clicked: null,
      searchDateDeals: [],
      closedDeals: [],
      openedDeals: []
    }
  },

  props: {
    deals: Object
  },

  methods: {

    popupRemoveToggle (deal) {
      this.popupRemoveHidden = !this.popupRemoveHidden
      this.currentDeal = deal
    },
    searchDealsByCustomer () {
      if (this.searchByCustomer === '') {
        this.data = _.toArray(this.deals)
        return this.data
      } else {
        this.data = this.data.filter(elem => {
          return elem.customer.name.includes(this.searchByCustomer)
        })
      }
      // }
    },
    showClosedDeals () {
      // if (this.clicked === true) {
      //   console.log(this.data)
      this.data = _.toArray(this.deals)
      //   this.clicked = false
      // } else {
      this.data = this.data.filter(el => {
        this.clicked = true
        return el.neededWorkersCount === `${el.workers.length}`
      })
      // }
      // this.closedDeals = this.data.filter(el => {
      //   return el.neededWorkersCount === `${el.workers.length}`
      // })
    },
    showOpenedDeals () {
      // if (this.clicked === true) {
      this.data = _.toArray(this.deals)
      //   this.clicked = false
      // } else {
      this.data = this.data.filter(el => {
        this.clicked = true
        return el.neededWorkersCount !== `${el.workers.length}`
      })
      // }
    },
    showAllDeals () {
      this.data = _.toArray(this.deals)
    }
  },
  watch: {
    // ПЕРЕПИСАТЬ ЧТОБ ПРИ ЧЕНДЖЕ НА ИНПУТ ВЫЗЫВАТЬ МЕТОД, ГДЕ НАДО МЕНЯТЬ МАССИВ, А НЕ В КОМПЬЮТЕД!!!!

    // searchArr () {
    // return this.data.filter(elem => {
    //   if (this.search === '') {
    //     // console.log(elem)
    //     return elem
    //   }
    //   return elem.dateStart.includes(this.search)
    // })
    // return this.data.filter(elem => {
    //   console.log(elem.dateStart)
    // })
    // }
    deals: function () {
      this.data = _.toArray(this.deals)
    }
  },
  created () {
    this.data = _.toArray(this.deals)
    this.searchDateDeals = this.data
    this.closedDeals = this.data
    this.openedDeals = this.data
  }
}
</script>

