<template>
  <PopupAddWrapper
    :hidden="popupHidden"
    header="Добавление заявки"
    @popup-add="validateDeal(addedDeal)"
    @popup-close="$emit('popup-toggle')"
  >
    <template #popup-add-content>
      <AppLineText
        inputID="input-deal-name"
        label="Название: "
        maxLength="40"
        v-model="addedDeal.name"
      />

      <AppLineDate
        dateID="input-deal-date-start"
        label="Начало: "
        v-model="dateStart"
      />

      <AppLineDate
        dateID="input-deal-date-end"
        label="Конец: "
        v-model="dateEnd"
      />

      <!-- <AppSelect
        selectID="select-customer"
        label="Заказчик: "
        v-model="addedDeal.customer"
      >
        <option v-for="element in customers" :key="element.id" :value="element.id">{{element.name}}</option>
      </AppSelect> -->
 <!-- <v-select data-app
          v-model="addedDeal.customer"
          :items="customers"
          label="Заказчик"
          item-text="item.name"
          item-value="item"
          persistent-hint
          return-object
          single-line
          @click = "test"
        ></v-select> -->
      <!-- <AppSelect
        selectID="select-customer"
        label="Заказчик: "
        v-model="addedDeal.customer"
      >
        <option v-for="element in customers" :key="element.id" :value="element">{{element.name}}</option>
      </AppSelect> -->
    <div class="select">
          <p class="select-label">Заказчик</p>
        <select
        class="app-select browser-default"
        v-model="addedDeal.customer"
        >
      <option v-for="element in customers" :key="element.id" :value="element">{{element.name}}</option>
    </select>
    </div>
     <AppSelect v-if="addedDeal.customer !== ''"
        selectID="select-subdivision"
        label="Подразделение: "
        v-model="addedDeal.subdivision"
      >
        <option v-for="el in addedDeal.customer.subdivisions" :key="el.id" :value="el.name">{{el.name}}</option>
      </AppSelect>

      <AppLineText
        inputID="input-deal-count"
        label="Кол-во рабочих: "
        maxLength="20"
        v-model="addedDeal.neededWorkersCount"
      />

      <div v-for="count in workersCount" :key="count">
        <AppSelect
          selectID="select-workers"
          :label="'Рабочий ' + (count) + ': ' "
          @select-change="pushMoreWorkers"
          :value="elementCountNumber(count)"
        >
          <option value="">Никого, выберите рабочего</option>
          <option v-for="element in localWorkers" :key="element.id">{{element.surname}} {{element.name}}</option>
        </AppSelect>
      </div>

      <p class="button-add" @click="addMoreWorkers()">Добавить ещё одного рабочего</p>
    </template>
  </PopupAddWrapper>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

import PopupAddWrapper from '../../popups/PopupAddWrapper'
import AppLineText from '../../AppLineText'
import AppLineDate from '../../AppLineDate'
import AppSelect from '../../AppSelect'

export default {
  name: 'DealPopupAdd',

  data () {
    return {
      addedDeal: {},
      localWorkers: '',
      workersCount: 1,
      addedWorkers: new Set(),
      dateStart: '',
      dateEnd: '',
      neededWorkersCount: '',
      validate: false
    }
  },

  computed: {
    ...mapGetters([
      'workers',
      'customers'
    ])
  },

  components: {
    PopupAddWrapper,
    AppLineText,
    AppLineDate,
    AppSelect
  },

  props: { popupHidden: Boolean },

  watch: {
    dateStart: function () {
      this.addedDeal.dateStart = this.dateStart
    },

    dateEnd: function () {
      this.addedDeal.dateEnd = this.dateEnd
    }
  },

  created () {
    this.renewAddedData()
    this.SET_WORKERS_FROM_LOCAL_STORAGE()
    this.SET_CUSTOMERS_FROM_LOCAL_STORAGE()
    this.checkWorkers()
  },

  methods: {
    ...mapMutations([
      'SET_WORKERS_FROM_LOCAL_STORAGE',
      'SET_CUSTOMERS_FROM_LOCAL_STORAGE'
    ]),
    elementCountNumber (count) {
      let i = 0
      for (const element of this.addedWorkers) {
        i++
        if (i === count) {
          return element
        }
      }
    },

    addMoreWorkers () {
      this.workersCount++
      this.checkWorkers()
    },

    pushMoreWorkers (workerID) {
      this.addedWorkers.add(workerID)
    },
    checkWorkers () {
      this.localWorkers = { ...this.workers }

      for (const element in this.localWorkers) {
        if (this.localWorkers[element].fired === 'Да') {
          delete this.localWorkers[element]
        }
      }

      if (this.dateStart !== '') {
        try {
          for (const element in this.localWorkers) {
            for (const event of this.localWorkers[element].events) {
              if (this.localWorkers[element] !== undefined && event.start < this.dateStart) {
                delete this.localWorkers[element]
              }
            }
          }
        } catch (e) {}
      }

      if (this.dateEnd !== '') {
        try {
          for (const element in this.localWorkers) {
            for (const event of this.localWorkers[element].events) {
              if (this.localWorkers[element] !== undefined && event.end < this.dateEnd) {
                delete this.localWorkers[element]
              }
            }
          }
        } catch (e) {}
      }
    },

    renewAddedData () {
      this.addedDeal = {
        id: Date.now(),
        name: 'Новая заявка',

        customer: '',
        workers: '',
        dateStart: '',
        dateEnd: '',
        workersCount: '',
        workersLength: 0,
        notes: [`${new Date().toLocaleDateString()} Заявка создана`],
        notesCount: '',
        // check
        subdivision: '',

        history: [{
          date: `[Дата: ${new Date().toLocaleDateString()} Время: ${new Date().toLocaleTimeString()}]`,
          info: 'Заявка создана',
          employee: {
            name: '',
            id: ''
          }
        }]
      }
    },

    // addDeal (deal) {
    //   if (this.validate === true) {
    //     deal.workers = [...this.addedWorkers]
    //     this.$emit('add-deal', deal)
    //     this.renewAddedData()
    //   }
    //   this.validateDeal()
    // },
    validateDeal (deal) {
      if (!this.dateStart) {
        // eslint-disable-next-line no-undef
        M.toast({ html: 'Внимание! Дата начала заявки не заполнена!' })
      } else if (!this.dateEnd) {
        // eslint-disable-next-line no-undef
        M.toast({ html: 'Внимание! Дата окончания заявки не заполнена!' })
      } else if (!this.addedDeal.customer) {
        // eslint-disable-next-line no-undef
        M.toast({ html: 'Внимание! Выберите заказчика!' })
      } else if (!this.addedDeal.neededWorkersCount) {
        // eslint-disable-next-line no-undef
        M.toast({ html: 'Внимание! Укажите количество рабочих!' })
      } else if (document.querySelector('#select-workers').value === '') {
        // eslint-disable-next-line no-undef
        M.toast({ html: 'Внимание! Выберите рабочего!' })
      } else {
        // eslint-disable-next-line no-undef
        M.toast({ html: 'Все поля заполнены, ты молодец-удалец!' })
        // return this.addDeal()
        this.validate = true
        deal.workers = [...this.addedWorkers]
        this.$emit('add-deal', deal)
        this.renewAddedData()
      }
    }
  }
}
</script>

<style>
  .popup-add-wrapper {
    display: flex;
    flex-direction: column;
    row-gap: 7px;
  }

  .button-add {
    color: #039be5;
    background: transparent;
    border: none;

    margin-top: 1rem !important;
    padding: 0;

    cursor: pointer;
  }

  .select {
    display: flex;
    column-gap: 14px;
    align-items: center;
  }

  .select-label {
    font-size: 1.2rem !important;
    color: #3e3e3e;
    line-height: 1.1rem;
    position: static !important;
  }

  select {
    border: 1px solid grey;
    height: fit-content !important;
  }
</style>
